.App {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #021b79;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.clb-footer {
  background: #01114b;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.header-icons {
  margin-left: 8px;
}

.clb-settings.revealed {
  background: #09d3ac;
  padding: 30px;
  border-radius: 30px;
  margin-left: 30px;
  margin-right: 30px;
}

.single-setting-area {
     margin-top: 20px;
}

.version {
     margin-top: 40px;
     font-size: 12px;
}

.clb-quiz-body-area {
     margin-bottom: 60px;
}

h2.quiz-active-message {
     font-weight: 300;
     font-size: 3em;
     margin-top: 40px;
     margin-bottom: 6px;
}

.type-of-question {
     font-size: 12px;
     color: #09d3ac;
}

.math-problem-numbers {
     margin: 30px;
     font-weight: 300;
     font-size: 4em;
}



#math-answer-form {
     font-size: 2em;
     font-weight: 300;
}

#math-answer-form input[type=number]
{
	background: transparent;
	border: none;
	border-bottom: 2px dashed #09d3ac;
	width: 150px;
	outline: none;
	padding: 0px 0px 0px 0px;
     font-size: 1em;
     font-weight: 300;
     margin-left: 8px;
}

#name-form {
     margin: 30px;
     font-size: 18px;
}

#name-form input {
     margin-left: 8px;
}

.clb-welcome-area p {
     font-weight: 300;
    font-size: 2em;
    margin-bottom: 30px;
}

button, input[type=submit] {
     background: #76B3FA;
     border: 0;
     border-radius: 8px;
     padding: 18px 30px;
     color: #fff;
     text-decoration: none;
     font-size: 16px;
     margin: 0 15px;
     transition: all ease-in-out .2s;
     text-transform: uppercase;
}

.clb-settings button, .clb-settings input[type=submit] {
     border: 0;
     border-radius: 5px;
     padding: 8px 14px;
     color: #fff;
     text-decoration: none;
     font-size: 13px;
     margin: 8px;
     transition: all ease-in-out .2s;
     text-transform: none;
}

button:hover, .clb-settings button:hover, input[type=submit]:hover {
     cursor: pointer;
     background: #01114b;
}

.clb-settings button {
     background: #021b79;
}

.clb-settings.revealed {
     display: flex;
     justify-content: space-between;
}

.single-setting-area.start-over {
     margin-top: 60px;
}

.single-setting-area.start-over button {
     background: red;
}

.single-setting-area.start-over button:hover {
     background: #000;
}

.summary-correct {
     margin-left: 6px;
     color: green;
}

.summary-wrong {
     margin-left: 6px;
     color: red;
}

.summary-area {
     background: #f5f5f5;
     padding-top: 30px;
     padding-bottom: 60px;
}

.current-score {
     font-weight: 300;
     font-size: 2em;
}

.clb-settings.hidden {
     position: fixed;
     right: -18px;
}

#start-quiz {
     margin: 30px;
}

button#start-quiz {
     padding: 30px 40px;
     font-size: 30px;
     background: linear-gradient(270deg,#01114b,#09d3ac);
    background-size: 400% 400%;
    -webkit-backface-visibility: hidden !important;
    backface-visibility: hidden !important;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-animation: AnimationName 6s ease infinite;
    -moz-animation: AnimationName 6s ease infinite;
    animation: AnimationName 6s ease infinite;
}

@-webkit-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@-moz-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
